<template>
  <div class="flex m-10">
    <ul class="list-group">
      <li class="list-group-item">
        <div class="item-handle"></div>
        <div class="item-cell">字段名</div>
        <div class="item-cell">字段类型</div>
      </li>
    </ul>
    <draggable
      class="list-group"
      tag="ul"
      :list="fieldList"
      v-bind="dragOptions"
      @start="isDragging = true"
      @end="isDragging = false"
    >
      <transition-group type="transition" name="flip-list">
        <li
          class="list-group-item"
          v-for="(element, index) in fieldList"
          :key="element.id"
        >
          <div class="item-handle">
            <unordered-list-outlined class="handle" />
          </div>
          <div class="item-cell">
            <a-input
              v-model:value="element.name"
              placeholder="请输入字段名"
              :allowClear="true"
            />
          </div>
          <div class="item-cell">
            <a-input
              v-model:value="element.type"
              @click="onFieldTypeClick(element)"
              placeholder="请选择字段类型"
            />
          </div>
          <div class="item-actions">
            <close-circle-outlined
              class="btn-action gray"
              @click="handleRemoveFieldRow(index)"
              title="移除当前字段"
            />
          </div>
        </li>
      </transition-group>
    </draggable>
    <div>
      <a-tooltip
        title="新增一行字段"
      >
        <a-button @click="handleCreateNewField()">
          <template #icon><PlusOutlined /></template>新增一行
        </a-button>
      </a-tooltip>
    </div>
    <a-modal
      v-model:visible="modalFieldType.visible"
      title="选择数据类型"
      :width="modalFieldType.width"
    >
      <template v-slot:footer>
        <div>
          <a-button>关 闭</a-button>
        </div>
      </template>
    </a-modal>
  </div>
</template>
<script>
import { reactive } from "vue";
import { VueDraggableNext } from "vue-draggable-next";
import {
  UnorderedListOutlined,
  CloseCircleOutlined,
  PlusOutlined,
  SearchOutlined,
} from "@ant-design/icons-vue";

export default {
  name: "home",
  components: {
    draggable: VueDraggableNext,
    UnorderedListOutlined,
    CloseCircleOutlined,
    PlusOutlined,
    SearchOutlined,
  },
  setup(props) {
    const fieldList = reactive([
      { id: 1, name: "Id", type: "rowNumber" },
      { id: 2, name: "first_name", type: "firstName" },
      { id: 3, name: "last_name", type: "lastName" },
      { id: 4, name: "email", type: "emailAddress" },
      { id: 5, name: "ip_address", type: "ipAddress" },
    ]);
    const modalFieldType = reactive({
      visible: false,
      width: 1000,
    });
    const onFieldTypeClick = () => {
      modalFieldType.visible = true;
    };
    const handleCreateNewField = () => {
      var row = {
        id: 0,
        name: "",
        type: "",
      };
      fieldList.push(row);
    };

    const handleRemoveFieldRow = (index) => {
      if (index > -1) {
        fieldList.splice(index, 1);
      }
    };

    return {
      fieldList,
      modalFieldType,
      onFieldTypeClick,
      handleCreateNewField,
      handleRemoveFieldRow,
    };
  },
  methods: {},
  computed: {
    dragOptions() {
      return {
        animation: 0,
        group: "description",
        disabled: false,
        ghostClass: "ghost",
      };
    },
  },
};
</script>
<style scoped>
.list-group {
  list-style: none;
  list-style-position: inside;
  margin: 0;
  padding: 0;
}
.list-group-item {
  display: flex;
  margin-bottom: 8px;
}
.item-handle {
  align-content: center;
  margin-top: 6px;
  width: 20px;
  display: block;
  margin-right: 5px;
}
.handle {
  fill: currentColor;
  display: inline-block;
  cursor: row-resize;
  font-size: 20px;
}
.item-cell {
  margin-right: 5px;
  min-width: 250px;
}
.flip-list-move {
  transition: transform 0.5s;
}
.no-move {
  transition: transform 0s;
}
.ghost {
  opacity: 0.5;
  background: #c8ebfb;
}
.list-group {
  min-height: 20px;
}
.list-group-item i {
  cursor: pointer;
}
.item-actions {
  width: 30px;
  align-content: center;
  align-items: center;
  display: flex;
}
.item-actions .btn-action {
  cursor: pointer;
  /* color: red; */
}
</style>
